<template>
    <div class="right__block">
        <div class="right__wrapper">
            <p>{{ list.title }}</p>
            <button @click="showShuffle = !showShuffle, shuffle()" v-if="!showShuffle">Перемешать</button>
            <button @click="showShuffle = !showShuffle, shuffle()" v-else>Сортировать</button>
        </div>
        <p v-for="item in list.items" v-if="item.showColors" v-show="!showShuffle">
            <button class="item__square" :style="{ backgroundColor: item.color }" v-for="n in item.amount"
                @click.prevent="decrementItem(item.id)"></button>
        </p>
        <p v-show="showShuffle">
            <button class="item__square" :style="{ backgroundColor: shuffledArray[n - 1] }"
                v-for="n in shuffledArray.length"
                @click.prevent="decrementItem(currentList.find(item => item.color === shuffledArray[n - 1]).id), shuffle()"></button>
        </p>
    </div>
</template>

<script>
import store from '@/store';
import { mapGetters } from 'vuex';

export default {
    props: ['list'],
    data() {
        return {
            shuffledArray: [],
            showShuffle: false,
            currentList: [],
        }
    },
    methods: {
        shuffle() {
            this.shuffledArray = [];
            return this.currentList.map(item => {
                for (let i = 0; i < item.amount; i++) {
                    this.shuffledArray.push(item.color);
                    this.shuffledArray = this.shuffledArray.sort(() => Math.random() - 0.5);
                };
            })
        },
        updateCurrentList() {
            if (this.currentList.length !== this.showList[this.list.id - 1].length) {
                return this.currentList = this.showList[this.list.id - 1];
            } else {
                return;
            }
        },
        decrementItem(itemId) {
            return store.commit('decrementItemAmount', { listId: this.list.id, itemId: itemId })
        },
    },
    computed: {
        ...mapGetters({ showList: 'listItemsShow' }),
    },
//     watch(
//   () => state.someObject,
//     (newValue, oldValue) => {
//     },
//     { deep: true }
// )
    // watch: {
    //     showList() {
    //         this.updateCurrentList();
    //     },        
    //     currentList() {
    //         this.shuffle();
    //     },
    // },
    watch: {
        showList: {
            handler(){
                this.updateCurrentList()
            },
            deep: true
        },
        currentList: {
            handler(){
                this.shuffle()
            },
            deep: true
        },
    }
}
</script>