<template>
    <li>
        <button @click="showList = !showList">&#8597</button>
        <input type="checkbox" name="Показать все/скрыть все" :checked="list.items.find(stateShow => stateShow.showColors === true)" @change.prevent="changeShowStates()">
        {{ list.title }}
        <ul v-show="showList">
            <li class="item" v-for="item in list.items" :key="item.id">
                <input type="checkbox" name="Показать/скрыть" v-model="item.showColors" @change.prevent="changeShowState(item.id, item.showColors)">
                <span>{{ item.title }}</span>
                <span>Количество: </span>
                <input class="input__amount" type="number" name="Количество" v-model.number="item.amount" @input.prevent="changeAmount(item.id, item.amount)">
                <input :style="{backgroundColor: item.color}" type="color" name="Цвет" v-model="item.color" @input.prevent="changeColor(item.id, item.color)">
            </li>
        </ul>
    </li>
</template>

<script>
import store from '@/store';

export default {
    props: ['list'],
    data() {
        return {
            showList: false, 
        }
    },
    methods: {
        changeAmount(itemId, itemAmount) {
            return store.commit('updateItemAmount', { listId: this.list.id, itemId: itemId, itemAmount: itemAmount })
        },
        changeColor(itemId, itemColor) {
            return store.commit('updateItemColor', { listId: this.list.id, itemId: itemId, itemColor: itemColor })
        },
        changeShowState(itemId, itemShowState) {
            return store.commit('updateItemShowState', { listId: this.list.id, itemId: itemId, itemShowState: itemShowState })
        },
        changeShowStates() {
            return store.commit('updateListShowStates', this.list.id)
        },
    },
}
</script>   